import { Component, OnInit, Input, NgZone } from '@angular/core';
import { Cloudinary } from '@cloudinary/angular-5.x';
import * as cloudinary from 'cloudinary-core';


@Component({
  selector: 'app-test-component',
  templateUrl: './test-component.component.html',
  styleUrls: ['./test-component.component.css']
})


export class TestComponentComponent implements OnInit {
  imgFile: string = '';
  userId: string = '';

  constructor() { }

  ngOnInit() { }

  registerPhoto(event: any) {
    if (event.target.files && event.target.files[0]) {
      this.imgFile = event.target.files[0];
    }
  }

  uploadPhoto() {
    const formData = new FormData();

    formData.append("file", this.imgFile);
    formData.append("upload_preset", "jvmpdbhl");
    formData.append("folder", this.userId);

    
    fetch('https://api.cloudinary.com/v1_1/dronicn8f/image/upload', {
      method: "POST",
      body: formData
    })
    .then((response) => {
      console.log(response);
      return response.text();
    })
    .then(data => {
      console.log(data);
    })
    .catch((err: Error) => {
      console.log(err);
    });
    
    // cloud.v2.uploader.unsigned_upload(this.imgFile, 'jvmpdbhl').then((response) => {
    //   console.log(response);
    //   return response.text();
    // })
    // .then(data => {
    //   console.log(data);
    // })
    // .catch((err: Error) => {
    //   console.log(err);
    // });

  }

  deletePic(pic: HTMLInputElement) {
    fetch(pic.value, {
      method: "DELETE"
    })
      .then(res => {
        console.log(res);
      })
      .catch((err: Error) => {
        console.log(err);
      });


  }


}
